
.dashboard {
  height: 55px;
  overflow: hidden;
  background: #f7f7f7;
  border-radius: 10px;
  padding: 8px;
  box-shadow: 0px -1px 0px 1px #fff inset, 0px 5px 0px #e3e3e3, 0px 5px 10px #cbcbcb;
  width: 900px;
  margin: 40px auto;
}
.dashboard .start-btn {
  font-weight: bold;
  cursor: pointer;
  float: left;
  height: 50px;
  line-height: 45px;
  text-align: center;
  border-radius: 5px;
  background: #DC143C;
  margin-right: 15px;
  font-size: 25px;
  color: #fff;
  text-shadow: 0px -1px 0px #500716;
  box-shadow: 0px 3px 10px #b2b2b2, 0px -5px 0px #ad102f inset, 0px 2px 0px 3px #f7f7f7, 0px 2px 0px 4px #cbcbcb, 0px -5px 1px #f16581 inset;
  padding: 0 30px;
}
.dashboard .start-btn:hover {
  background: #e5153f;
}
.dashboard .start-btn:active {
  text-shadow: 0px 0px 0px #500716;
  line-height: 46px;
  background: #c51236;
  box-shadow: 0px 2px 0px #f1f1f1 inset, 0px 2px 2px #b2b2b2, 0px 2px 0px 3px #f1f1f1, 0px 2px 0px 4px #cbcbcb, 0px -1px 0px #7f0b22 inset;
}
.dashboard .undo-btn {
  font-weight: bold;
  cursor: pointer;
  float: left;
  height: 50px;
  line-height: 45px;
  text-align: center;
  border-radius: 5px;
  background: #4169e1;
  font-size: 25px;
  color: #fff;
  text-shadow: 0px -1px 0px #132c76;
  box-shadow: 0px 3px 10px #b2b2b2, 0px -5px 0px #214cce inset, 0px 2px 0px 3px #f7f7f7, 0px 2px 0px 4px #cbcbcb, 0px -5px 1px #99afef inset;
  padding: 0 30px;
}
.dashboard .undo-btn:hover {
  background: #4a70e2;
}
.dashboard .undo-btn:active {
  text-shadow: 0px 0px 0px #132c76;
  line-height: 46px;
  background: #2b58de;
  box-shadow: 0px 2px 0px #f1f1f1 inset, 0px 2px 2px #b2b2b2, 0px 2px 0px 3px #f1f1f1, 0px 2px 0px 4px #cbcbcb, 0px -1px 0px #1a3ca2 inset;
}
.dashboard .camp {
  box-shadow: 0px 2px 0px #cbcbcb inset, 0px 3px 0px #f1f1f1 inset, 0px 1px 0px 1px #f1f1f1,  0px 1px 0px 2px #f7f7f7;
  padding: 1px;
  border-radius: 5px;
  width: 90px;
  background: #fff;
  float: right;
  height: 40px;
}
.dashboard .switch {
  box-shadow: 0px -5px 0px #cc3700 inset, 0px -6px 0px #ff6a33 inset, 0px -5px 0px #FF4500;
  transition: .2s all;
  width: 60px;
  height: 40px;
  border-radius: 5px;
  background: #FF4500;
  color: #fff;
  line-height: 30px;
  font-weight: bold;
  text-shadow: 0px -1px 0px 1px #cc3700;
}
.dashboard .switch.active {
  margin-left: 30px;
  background: #1E90FF;
  text-shadow: 0px -1px 0px #0077ea;
  box-shadow: 0px -5px 0px #0077ea inset, 0px -6px 0px #51a9ff inset, 0px -5px 0px #1E90FF;
}



